import React from "react";

const Banner = () => {
  return (
    <div className="p-4 p-md-5 mb-4 rounded text-body-emphasis bg-custom">
      <div className="col-lg-6 px-0 content-banner">
        <h1 className="display-4 fst-italic">
          Title of a longer featured blog post
        </h1>
        <p className="lead my-3">
          Multiple lines of text that form the lede, informing new readers
          quickly and efficiently about what’s most interesting in this post’s
          contents.
        </p>
        <p className="lead mb-0 content-banner-continuereading">
          <a className="text-body-emphasis fw-bold" href="/">
            Continue reading...
          </a>
        </p>
      </div>
    </div>
  );
};

export default Banner;
